@import './color.scss';

%text-25px {
    font-size: 25px;
    font-weight: 700;
}

%small-text {
    font-size: 12px;
    line-height: 15px;  
}

%header-footer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: white;
    padding: 45px 0;
}

%news-date {
    span {
        &:first-child {
            font-size: 30px;
            color: $primary;

            @media screen and (max-width: 768px) {
                font-size: 20px;
            }
        }
    }
}

%hamburger {
    width: 25px;
    height: 2px;
    display: block;
    background-color: $primary;
    transition: .5s;    
}