@import '../../scss/mixins.scss';

.footer__wrap {
    @extend %header-footer;

    @media screen and (max-width: 480px) {
        flex-wrap: wrap;
        justify-content: space-evenly;
    }
    @media screen and (max-width: 425px) {
        flex-direction: column;
    }

    .logo {
        @media screen and (max-width: 425px) {
            text-align: center;
        }
    }
}

.footer-text {
    font-size: 15px;

    @media screen and (max-width: 480px) {
        order: 3;
        margin: 30px 0;
    }
    @media screen and (max-width: 425px) {
        order: 0;
        margin: 30px 0 0;
    }
}

.made-by {
    text-align: right;

    @media screen and (max-width: 425px) {
        text-align: center;
        margin-top: 30px;
    }

    span {
        @extend %small-text;
    }

    p {
        @extend %text-25px;
    }
}