@import '../../scss/_color.scss';
@import '../../scss/_mixins.scss';

.news-page{
    display: flex;

    @media screen and (max-width: 992px) {
        flex-direction: column;
    }

    &__title{
        span {
            color: $accent;
        }

        @media screen and (max-width: 576px) {
            font-size: 36px;
        }
    }
    &__author{
        font-size: 25px;
        color: $gray;
        margin: 30px 0;

        @media screen and (max-width: 992px) {
            display: inline-block;
            margin: 30px 30px 0 0;
        }
        @media screen and (max-width: 576px) {
            font-size: 16px;
        }
    }
    &__left-side{
        width: 32%;
        @media screen and (max-width: 992px) {
            width: 100%;
        }
    }
    &__published{
        @extend %news-date;
        color: $gray;

        @media screen and (max-width: 992px) {
            display: inline-block;
        }
    }
    &__right-side{
        width: 68%;
        margin-left: 30px;
        @media screen and (max-width: 992px) {
            width: 100%;
            margin: 30px 0 0;
        }
    }
    &__main-img {
        margin-bottom: 60px;
    }
    &__content {
        font-size: 25px;

        @media screen and (max-width: 992px) {
            font-size: 16px;
        }
    }
    &__content-wrap {
        &:not(:last-child) {
            margin-bottom: 30px;
        }

        h2 {
            font-size: 35px;
            margin-bottom: 20px;

            @media screen and (max-width: 576px) {
                font-size: 26px;
            }
        }

        img {
            margin-top: 20px;
        }

        iframe {
            width: 100%;
            height: 408px;
            margin-top: 20px;
        }
    }
    &__quote {
        padding-left: 30px;
        position: relative;

        &::before {
            content: '';
            width: 4px;
            height: 100%;
            background-color: $accent;
            display: block;
            position: absolute;
            top: 0;
            left: 0;
        }
    }
}