@import '../../scss/mixins.scss';

.header {
    @extend %header-footer;
    max-width: 1110px;
    width: 100%;
    transform: translateX(50%);
    position: fixed;
    top: 0;
    right: 50%;
    z-index: 10;

    @media screen and (max-width: 1200px) {
        width: 90%;
    }

    &.active {
        max-width: 100%;
        width: 100%;
        height: 100%;
    }
}