@import '../../scss/_color.scss';
@import '../../scss/_mixins.scss';

.news {
    margin-top: 75px;

    &__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        gap: 30px;

        @media screen and (max-width: 768px) {
            gap: 15px;
        }

        @media screen and (max-width: 640px) {
            grid-template-columns: repeat(2, 1fr);
        }
        @media screen and (max-width: 400px) {
            gap: 10px;
        }
    }
    
    &__item {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 24px 0 21px;
        min-height: 245px;
        cursor: pointer;

        @media screen and (max-width: 992px) {
            min-height: 200px;
        }
    }
    
    &__title {
        font-size: 25px;
        padding: 0 23px 0 25px;
        transition: all .4s ease;
        position: relative;
        z-index: 2;
        
        &::before {
            content: '';
            display: block;
            width: 2px;
            height: 100%;
            background-color: $accent;
            transition: all .4s ease;
            position: absolute;
            left: 0;
            bottom: 0;
            z-index: -1;
        }

        &:hover {
            color: white;

            &::before {
                width: 100%;
            }
        }

        @media screen and (max-width: 992px) {
            font-size: 20px;
        }
        @media screen and (max-width: 400px) {
            font-size: 15px;
        }
        @media screen and (max-width: 356px) {
            padding: 0 20px 0 10px;
        }
    }
    
    &__inner-wrapper {
        font-size: 20px;
        color: $gray;
        display: flex;
        align-items: flex-end;
        justify-content: space-between;
        padding: 0 25px;

        @media screen and (max-width: 400px) {
            padding: 0 25px 0 0;
        }
    }
    
    &__author {
        @media screen and (max-width: 992px) {
            font-size: 16px;
        }
        @media screen and (max-width: 576px) {
            width: 50%;
        }
        @media screen and (max-width: 400px) {
            font-size: 13px;
        }
    }

    &__published {
        @extend %news-date;

        @media screen and (max-width: 576px) {
            width: 50%;
            display: flex;
            justify-content: flex-end;
        }
    }
}