@import './scss/_color.scss';
@import './scss/_mixins.scss';

.App {
  font-family: 'Proxima Nova';
  color: $primary;
}

.container {
  max-width: 1110px;
  width: 100%;
  margin: 0 auto;

  @media screen and (max-width: 1200px) {
      width: 90%;
  }
}

.content-wrap {
  position: relative;
}

main {
  padding: 210px 0 140px;

  @media screen and (max-width: 356px) {
      padding: 150px 0 20px;
  }
}

.logo {
  @extend %text-25px;

  span {
    @extend %small-text;
    font-weight: 400;
    display: block;
  }
}

.page-title {
  font-size: 48px;

  .accent {
    color: $accent;
  }
}

img {
  max-width: 100%;
  width: 100%;
}

h1:after{
	content:attr(data-text);
	color:red;
	transform:translateX(-100%);
	position:absolute;
}

a {
  transition: .5s;
}