@import '../../scss/_color.scss';
@import '../../scss/_mixins.scss';

.contacts{
    font-size: 20px;

    &__wrapper {
        display: flex;
        justify-content: space-between;

        @media screen and (max-width: 1152px) {
            flex-direction: column;
        }

        img {
            width: auto;
            object-fit: cover;

            @media screen and (max-width: 1152px) {
                max-width: 700px;
                width: 100%;
                margin: 0 auto;
            }
        }
    }
    &__leftside {
        @media screen and (max-width: 1152px) {
            margin-bottom: 30px;
        }
    }
    &__title {
        display: none;
    }
    &__phone {
        font-size: 60px;
        line-height: 93%;
        color: $accent;

        &:hover {
            color: $primary;
        }

        @media screen and (max-width: 556px) {
            font-size: 30px;
        }
    }
    &__made-by {
        font-size: 25px;
        padding-left: 23px;
        margin: 50px 0 25px;        
        width: 150px;
        position: relative;

        &::before {
            content: '';
            width: 2px;
            height: 100%;
            display: block;
            background-color: $accent;
            position: absolute;
            top: 0;
            left: 0;
        }
    }
    &__email {
        display: block;
        margin-bottom: 52px;

        &:hover {
            color: $accent;
        }
    }
    &__skills-wrapper {
        p {
            color: $gray;
            margin-bottom: 25px;
        }

        span {
            &.accent {
                color: $accent;
            }
        }
    }
}