html,
body,
#root,
.App,
.container,
.content-wrap {
	height: 100%;
  max-height: max-content;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Proxima Nova';
  src: url('./fonts/ProximaNovaCond-Bold.eot');
  src: url('./fonts/ProximaNovaCond-Bold.eot?#iefix') format('embedded-opentype'),
      url('./fonts/ProximaNovaCond-Bold.woff2') format('woff2'),
      url('./fonts/ProximaNovaCond-Bold.woff') format('woff'),
      url('./fonts/ProximaNovaCond-Bold.ttf') format('truetype'),
      url('./fonts/ProximaNovaCond-Bold.svg#ProximaNovaCond-Bold') format('svg');
  font-weight: bold;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Proxima Nova';
  src: url('./fonts/ProximaNova-Regular.eot');
  src: url('./fonts/ProximaNova-Regular.eot?#iefix') format('embedded-opentype'),
      url('./fonts/ProximaNova-Regular.woff2') format('woff2'),
      url('./fonts/ProximaNova-Regular.woff') format('woff'),
      url('./fonts/ProximaNova-Regular.ttf') format('truetype'),
      url('./fonts/ProximaNova-Regular.svg#ProximaNova-Regular') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

/*  Reset: Start  */
* {
  vertical-align: baseline;
  font-weight: inherit;
  font-family: inherit;
  font-style: inherit;
  font-size: 100%;
  border: 0 none;
  outline: 0;
  padding: 0;
  margin: 0;
}

div, p, form, input, a, span, button {
box-sizing: border-box;
}

ul, li {
display: block;
margin: 0;
  padding: 0;
}

input, button, button:focus {
border: none;
outline: none;
}

input, input:hover, input:focus, input:active,
button, button:hover, button:focus, button:active,
a, a:hover, a:focus {
outline: none;
}

table {
  display: table;
  border-collapse: separate;
  border-spacing: 0;
  border-color: transparent;
}

a {
  font-family: 'Proxima Nova';
  text-decoration: none;
  color: inherit;
}
/*  Reset: End  */