@import '../../scss/_color.scss';
@import '../../scss/_mixins.scss';

.nav {
    ul {
        display: flex;

        li {
            &:not(:last-child) {
                margin-right: 35px;
            }

            a {
                &::after {
                    content: '';
                    width: 0;
                    height: 1px;
                    display: block;
                    background-color: $accent;
                    opacity: 0;
                    transition: all .5s ease;                    
                }

                &:hover {
                    color: $accent;

                    &::after {
                        width: 100%;
                        opacity: 1;
                    }
                }

                &.current-page {
                    color: $accent;
                    cursor: default;

                    &::after {
                        width: 0;
                    }
                }
            }
        }

        @media screen and (max-width: 480px) {
            display: none;
        }
    }

    &.active {
        width: 100%;
        height: 100%;
        background-color: $primary;
        display: flex;
        flex-direction: column;
        position: fixed;
        top: 0;
        left: 0;

        ul {
            display: block;
            order: 2;
            
            li {
                &:not(:last-child) {
                    margin-right: 0;
                }

                a {
                    color: white;
                    text-align: center;
                    display: block;
                    padding: 15px;

                    &:hover, &:focus {
                        background-color: #252d3c;

                        &::after {
                            width: 0%;
                            opacity: 0;
                        }
                    }
                }
            }
        }

        .hamburger-wrap {            
            padding: 30px;
            text-align: right;
        }

        .hamburger {
            background-color: $primary;
            order: 0;

            span {
                width: 0;
                visibility: hidden;
            }

            &::before, &::after, span {
                background-color: white;
            }

            &::before {
                transform: rotate(-45deg) translateY(-50%);
            }
            
            &::after {
                transform: rotate(45deg) translateY(-50%);
                top: 50%;
            }
        }
    }

    .hamburger {
        display: none;
        height: 16px;
        background-color: transparent;
        position: relative;

        &::before, &::after {
            content: '';
            @extend %hamburger;
            position: absolute;
        }

        &::before {
            top: 50%;
            transform: translateY(-50%);
        }

        &::after {
            bottom: 0;
        }

        span {
            @extend %hamburger;         
        }

        @media screen and (max-width: 480px) {
            display: inline-block;
            width: 25px;
        }
    }
}